                        <div class="col-12">
                            <Search @submit-form="loadData"></Search>
                        </div>
                    </div>
                    <transition mode="out-in" name="pop" appear>
                        <Alert v-if="!$store.getters.dataLoaded && !$store.getters.isLoading && $store.getters.checkIsDev" @click="loadData"/>
                    </transition>
                    <div class="d-block d-lg-none col-2"></div>
                </div>
            </transition>
            <transition mode="out-in" name="pop" appear>
                <div class="col-12 mb-3" v-if="!$store.getters.dataLoaded && this.checkIsError.error || $store.getters.dataLoaded && !$store.getters.isLoading && this.$store.getters.loadData['counter'] != ''">
                    <dividend-output :data="FilteredResult" :error="this.checkIsError.error"></dividend-output>
                </div>
            </transition>
        </div>
        <transition mode="out-in" name="pop" appear>
            <div class="row" v-if="!this.checkIsError.error && !$store.getters.isLoading && !$store.getters.dataLoaded">
                <div class="d-block d-lg-none col-2"></div>
