<script>
import Header from './pages/Header.vue';
import Footer from './pages/Footer.vue';
import Alert from './components/UI/Alert.vue';
import Search from './components/UI/Search.vue';
import DividendOutput from './pages/DividendOutput.vue';
import LoadingSpinner from './components/UI/LoadingSpinner.vue';
import Modal from "./components/UI/Modal.vue" 

export default {
    name: 'SavaJava Global Stocks Dividend Reliability & Analysis Toolkit',
    components: {
    Header,
    Footer,
    Alert,
    Search,
    DividendOutput,
    LoadingSpinner,
    Modal
},
    data(){
        return {
            FilteredResult: [],
            isError: null,
            isOpen: false,
            isSafari: false,
        }
    },
    mounted(){
        this.statusReport();
        this.colorSchemeWatcher();
        this.isBrowserCompatibility();
    },
    updated(){
        this.statusReport();
    },
    computed: {
        isLight(){
            return this.$store.getters.checkIsLight;
        },
        isDev(){
            return this.$store.getters.checkIsDev;
        },
        getData(){
            return this.$store.getters.loadData;
        },
        isLoading(){
            return this.$store.getters.isLoading;
        },
        dataLoaded(){
            return this.$store.getters.dataLoaded;
        },
        checkIsError(){
            return this.$store.getters.isError;
        },
        checkDisclaimerIsOpen(){
            return this.$store.getters.disclaimerIsOpen;
        },
        searchCount(){
            return this.$store.getters.searchCount;
        },
        searchCountCookie(){
            return this.$store.getters.searchCountCookie;
        },
    },
    methods: {
        colorSchemeWatcher(){
            let body = document.querySelector('body');
            if(window.matchMedia('(prefers-color-scheme: dark)').matches){
                body.classList.remove('light');
                return this.$store.dispatch('toggleLightMode', false);
            }
            body.classList.add('light');
            return this.$store.dispatch('toggleLightMode', true);
        },
        statusReport(){
            this.isError = this.checkIsError;
            console.log(this.isError)
        },
        toggleDarkMode(){
            let body = document.querySelector('body');
            this.$store.dispatch('toggleLightMode');
            body.classList.toggle('light');
        },
        loadData(){
            if( this.$store.getters.loadData.status !== 404 ){
                if(this.$store.getters.loadData[0]["Stock Type"]==="Non-Dividend"){
                    return this.FilteredResult = this.$store.getters.loadData[0];
                }
                if(this.$store.getters.loadData["Stock Type"]==="Dividend"){
                    return this.FilteredResult = this.$store.getters.loadData;
                }
            } else {
                return this.FilteredResult = this.$store.getters.loadData;
            }
        },
        openDisclaimer(){
            this.$store.dispatch('openDisclaimer');
            console.log(this.checkDisclaimerIsOpen);
        },
        isBrowserCompatibility(){
            return this.isSafari = navigator.userAgentData.brands[0].brand.includes('Safari');
        },
    },
}
</script>

